<template><section><h1>Collapse 折叠面板</h1>
<template>
    <coding title="折叠面板" content="最简单的折叠面版" lan="html" :code="code1">
        <y-collapse :default-active-key="[1]" @changes="test">
            <y-panel header="asfaasdfasdf" :col="1">asd</y-panel>
            <y-panel header="asfaasdfasdf" :col="2">asd</y-panel>
        </y-collapse>
    </coding>
    <coding title="手风琴" content="一次只能打开一个" lan="html" :code="code2">
        <y-collapse :default-active-key="[1,]" accordion="">
            <y-panel header="asfaasdfasdf" :col="1">asd</y-panel>
            <y-panel header="asfaasdfasdf" :col="2">asd</y-panel>
            <y-panel header="asfaasdfasdf" :col="3">asd</y-panel>
        </y-collapse>
    </coding>
</template>

<h2>参数设置</h2>
<h3>Collapse</h3>
<table>
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
<th>可选值</th>
</tr>
</thead>
<tbody>
<tr>
<td>defaultActiveKey</td>
<td>默认打开的panel</td>
<td><code>Array</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>accordion</td>
<td>手风琴</td>
<td><code>Boolean</code></td>
<td><code>false</code></td>
<td>-</td>
</tr>
</tbody>
</table>
<h3>Panel</h3>
<table>
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
<th>可选值</th>
</tr>
</thead>
<tbody>
<tr>
<td>col</td>
<td>键值，必须，不能重复</td>
<td><code>Number</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>header</td>
<td>面板头内容，必须</td>
<td><code>String</code></td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
<h2>Events</h2>
<table>
<thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>changes</td>
<td>改变时触发</td>
<td>打开的panel的col</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
    data () {
        return {
            code1: 
`<y-collapse :default-active-key="[1]" @changes="test">
    <y-panel header="asfaasdfasdf" :col="1">asd</y-panel>
    <y-panel header="asfaasdfasdf" :col="2">asd</y-panel>
</y-collapse>
`,
            code2:
`<y-collapse :default-active-key="[1, 3]" accordion>
    <y-panel header="asfaasdfasdf" :col="1">asd</y-panel>
    <y-panel header="asfaasdfasdf" :col="2">asd</y-panel>
    <y-panel header="asfaasdfasdf" :col="3">asd</y-panel>
</y-collapse>`
        }
    },
    methods: {
        test (value) {
            console.log(value)
        }
    }
}
</script>


